// router/index.js（Vue 3 语法）
import { createRouter, createWebHistory } from 'vue-router'
import ULogin from '../components/ULogin.vue' // 引入登陆路由
import URegister from '../components/URegister.vue' // 引入注册路由
import UserHome from '../home/UserHome.vue'  // 引入home页面路由
import Message from "element-plus" //引入导航守卫登陆UI按钮

const router = createRouter({
  history: createWebHistory(),
  routes: [
    { 
      path: '/',
      redirect: '/login' // 避免无限重定向
    },
    {
      path: '/login',
      name: 'Login',
      component: ULogin // 确保组件名与导入一致
    },
    {
        path: '/register',
        name: 'Register',
        component: URegister // 确保组件名与导入一致
    },
    {
        path: '/home',
        name: 'UserHome',
        component: UserHome // 确保组件名与导入一致
    }
  ]
})

// 导航守卫，前置处理
router.beforeEach((to, from, next) => {
    let isAuthenticated = !!sessionStorage.getItem('userInfo')
    // 如果路由要跳转到除了登录和注册的界面的话就判断是否已经登录，如果没有登录就强制跳到登录界面
    if (to.path !== '/login' && to.path !== '/register' && !isAuthenticated) {
        next({ path: '/login' })
        Message({
            message: '请先登录！',
            type: "warning",
        });
    } else next()
})



export default router;


