<template>
  <div class="todo-app">
    <div class="welcome">
      <div class="title">欢迎 {{ user.uname }}！</div>
      <router-link to="/login">
       <el-button round type="primary" size="medium">退出</el-button>
      </router-link>
    </div>

  <div class="todo-form">
  <input
    v-model="value"
    type="text"
    class="todo-input"
    placeholder="在这里输入待办事项..."
  />
  <div @click="add" class="todo-button">确认添加</div>
  </div>
  <div 
  v-for="(item) in list"
  :key="item._id"
  :class="[item.isCompleted ? 'completed' : 'item']"
>
  <div>
    <input @click="update(item._id)" v-model="item.isCompleted" type="checkbox" />
    <span class="name">{{ item.value }}</span>  <!-- 加入index序号标签-->
  </div>
  <div @click="del(item._id)" class="del">删除事项</div>
  </div>
  </div>
  </template>

  <script setup>
  import { ref, onMounted } from 'vue'
  import axios from 'axios'
  const user = ref({ uname: "" }) //创建一个user变量
  getlist()
  const value = ref('')
  const list = ref([])

  onMounted(() => {
    if (sessionStorage.getItem('userInfo')) {
      // 将用户信息存储到sessionStorage中
      user.value = JSON.parse(sessionStorage.getItem('userInfo'))
    }
  })

async function getlist(){
  const res= await axios({
    url:"https://lnlyimrcxo.hzh.sealos.run/get_list",
    method:'GET',
  })
  list.value =res.data.list
}

async function add() {
  await axios({
    url:"https://lnlyimrcxo.hzh.sealos.run/add_list",
    method:'POST',
    data:{
      value: value.value,
      isCompleted:false,
    },
  })
  getlist()

  value.value = ''
}

async function update(id) {
  await axios({
    url:"https://lnlyimrcxo.hzh.sealos.run/update_list",
    method:'POST',
    data:{
      id,
    }
  })
  getlist()
}

async function del(id) {
  await axios({
    url:"https://lnlyimrcxo.hzh.sealos.run/del_list",
    method:'POST',
    data:{
      id:id
    }
  })

getlist()
}
  </script>
  
  <!-- 样式保持不变 -->
  <style scoped>
  .todo-app {
    box-sizing: border-box;
    margin-top: 40px;
    margin-left: 1%;
    padding-top: 30px;
    width: 98%;
    height: 500px;
    background: #ffffff;
    border-radius: 40px;
  }
  .welcome {
     display: flex;
    justify-content: center;  /* 主轴居中 */
    align-items: center;      /* 交叉轴居中 */
    gap: 10px;
   }
   
  .title {
    text-align: center;
    font-size: 30px;
    font-weight: 700;
  }
  
  .todo-form {
    display: flex;
    justify-content: center;
    margin: 20px 0 30px 20px;
  }
  
  .todo-button {
    width: 100px;
    height: 52px;
    border-radius: 0 20px 20px 0;
  
    text-align: center;
    background: linear-gradient(
      to right,
      rgb(113, 65, 168),
      rgba(44, 114, 251, 1)
    );
    color: #fff;
    line-height: 52px;
    cursor: pointer;
    font-size: 14px;
    user-select: none;
  }
  
  .todo-input {
    padding: 0px 15px 0px 15px;
    border-radius: 20px 0 0 20px;
    justify-content: space-between;
    border: 1px solid #dfe1e5;
    outline: none;
    width: 60%;
    height: 50px;
  }
  
  .item {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 80%;
    height: 50px;
    margin: 8px auto;
    padding: 16px;
    border-radius: 20px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 20px;
  }
  
  .del {
    color: red;
  }
  
  .completed {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 80%;
    height: 50px;
    margin: 8px auto;
    padding: 16px;
    border-radius: 20px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 20px;
    text-decoration: line-through;
    opacity: 0.4;
  }
  </style>